<template>
    <div ref="arContainer" class="ar-container">
        <canvas ref="threeCanvas"></canvas>
    </div>
</template>

<script>
    import * as THREE from 'three';
    import { DeviceOrientationControls } from 'three/examples/jsm/controls/DeviceOrientationControls.js';
    import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
    import { markRaw } from 'vue';

    export default {
        data() {
            return {
                scene: markRaw(new THREE.Scene()),
                camera: markRaw(new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000)),
                renderer: null,
                cube: null,
                video: null,
                videoTexture: null,
                controls: null,
                baseDistance: 5,
                zAccelerationThreshold: 0.05,
                currentZAcceleration: 0,
                alpha: 0,  // 用于存储 alpha 值
                beta: 0,   // 用于存储 beta 值
                gamma: 0   // 用于存储 gamma 值
            };
        },
        methods: {
            async initCamera() {
                try {
                    this.video = document.createElement('video');
                    this.video.autoplay = true;

                    const stream = await navigator.mediaDevices.getUserMedia({
                        video: { facingMode: { exact: 'environment' } }
                    });

                    this.video.srcObject = stream;
                    await this.video.play();

                    this.videoTexture = markRaw(new THREE.VideoTexture(this.video));
                    this.videoTexture.minFilter = THREE.LinearFilter;
                    this.videoTexture.magFilter = THREE.LinearFilter;
                    this.videoTexture.format = THREE.RGBFormat;

                    this.scene.background = this.videoTexture;
                } catch (error) {
                    console.error('Error accessing camera:', error);
                }
            },
            initThreeJS() {
                const container = this.$refs.arContainer;

                this.renderer = markRaw(new THREE.WebGLRenderer({ antialias: true, alpha: true, canvas: this.$refs.threeCanvas }));
                this.renderer.setSize(container.clientWidth, container.clientHeight);

                // 创建立方体
                const geometry = new THREE.BoxGeometry(1, 1, 1);
                const material = new THREE.MeshNormalMaterial();
                this.cube = markRaw(new THREE.Mesh(geometry, material));
                this.cube.position.set(0, 0, -5);

                this.scene.add(this.cube);

                // 设置相机
                this.camera.position.set(0, 0, this.baseDistance);
                this.camera.lookAt(this.cube.position);

                // 判断设备并启用控制器
                if (this.isMobileDevice()) {
                    this.controls = new DeviceOrientationControls(this.camera);
                    this.controls.connect();  // 确保启用控制器
                    this.controls.alphaOffset = -Math.PI / 2; // 调整初始角度
                    this.startDeviceMotionListener();
                    window.addEventListener('deviceorientation', this.handleDeviceOrientation);
                } else {
                    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
                    this.controls.enableDamping = true;
                    this.controls.dampingFactor = 0.1;
                    this.controls.enableZoom = true;
                }

                this.animate();
            },
            animate() {
                requestAnimationFrame(this.animate);

                // 更新控制器
                if (this.controls) this.controls.update();

                // 更新相机位置
                if (this.isMobileDevice()) {
                    this.updateCameraDistance();
                }

                this.renderer.render(this.scene, this.camera);
            },
            resize() {
                const container = this.$refs.arContainer;
                this.camera.aspect = container.clientWidth / container.clientHeight;
                this.camera.updateProjectionMatrix();
                this.renderer.setSize(container.clientWidth, container.clientHeight);
            },
            startDeviceMotionListener() {
                window.addEventListener('devicemotion', (event) => {
                    const zAccel = event.accelerationIncludingGravity.z || 0;
                    this.currentZAcceleration = zAccel;
                });
            },
            updateCameraDistance() {
                if (Math.abs(this.currentZAcceleration) > this.zAccelerationThreshold) {
                    this.baseDistance -= this.currentZAcceleration * 0.1;
                    this.baseDistance = Math.max(1, Math.min(this.baseDistance, 15));
                    this.camera.position.z = this.baseDistance;
                }
            },
            handleDeviceOrientation(event) {
                // 处理设备方向变化
                this.alpha = event.alpha ? THREE.Math.degToRad(event.alpha) : 0;  // 绕Z轴的旋转
                this.beta = event.beta ? THREE.Math.degToRad(event.beta) : 0;      // 绕X轴的旋转
                this.gamma = event.gamma ? THREE.Math.degToRad(event.gamma) : 0;   // 绕Y轴的旋转

                // 使用陀螺仪数据来控制物体的旋转
                this.cube.rotation.set(this.beta, this.alpha, -this.gamma);
            },
            isMobileDevice() {
                return /Android|iPhone|iPad|iPod/i.test(navigator.userAgent);
            }
        },
        async mounted() {
            await this.initCamera();
            this.initThreeJS();
            window.addEventListener('resize', this.resize);
        },
        beforeDestroy() {
            window.removeEventListener('resize', this.resize);
            window.removeEventListener('devicemotion', this.startDeviceMotionListener);
            window.removeEventListener('deviceorientation', this.handleDeviceOrientation);
            if (this.video && this.video.srcObject) {
                const stream = this.video.srcObject;
                const tracks = stream.getTracks();
                tracks.forEach(track => track.stop());
            }
        }
    };
</script>
