<template>
    <div>
        <video ref="video" autoplay playsinline style="display:none"></video>
        <div>
            <canvas id="output" style="width: calc(100vw - 16px); height: calc(100vh  - 130px);"></canvas>
        </div>
        <div>
            <button @click="startRecognition">辨識</button>
        </div>
    </div>
</template>

<script>
    import { markRaw } from 'vue';
    import { ref, onMounted } from 'vue';
    import * as poseDetection from '@tensorflow-models/pose-detection'
    import * as tf from '@tensorflow/tfjs-core'
    import '@mediapipe/face_mesh'
    import '@tensorflow/tfjs-backend-webgl'
    import * as THREE from 'three'
    import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
    import { Pose } from "kalidokit";



    export default {
        name: 'PoseRecognitionComponent',
        setup() {
            const video = ref(null);
            const model = ref(null);

            const setupCamera = async () => {
                const stream = await navigator.mediaDevices.getUserMedia({ video: true });
                video.value.srcObject = stream;

                video.value.width = 320;
                video.value.height = 280;
                return new Promise((resolve) => {
                    video.value.onloadedmetadata = () => {
                        resolve(video.value);
                    };
                });
            };

            const createModel = async () => {
                await tf.setBackend('webgl');
                model.value = markRaw(await poseDetection.createDetector(
                    poseDetection.SupportedModels.BlazePose, {
                    runtime: 'mediapipe',
                    solutionPath: `https://cdn.jsdelivr.net/npm/@mediapipe/pose`
                }
                ));
                //model.value = markRaw(await poseDetection.createDetector(
                //    poseDetection.SupportedModels.PoseNet, {
                //        quantBytes: 4,
                //        architecture: 'MobileNetV1',
                //        outputStride: 16,
                //        inputResolution: { width: 500, height: 500 },
                //        multiplier: 0.75
                //}
                //));
            };

            const recognition = async () => {
                try {
                    //const poses = await model.value.estimatePoses(video.value, { flipHorizontal: false });
                    //const [poses, canvasInfoTemp] = await model.value.estimatePoses(
                    //    video.value,
                    //    { maxPoses: 1, flipHorizontal: false });
                    const poses = await model.value.estimatePoses(
                        video.value,
                        { maxPoses: 1, flipHorizontal: false });

                    for (let index = 0; index < poses.length; index++) {
                        if (poses.length > index) {
                            const keypoints = poses[index].keypoints;
                            const keypoints3D = poses[index].keypoints3D
                            const poseResult = Pose.solve(keypoints3D, keypoints, {
                                runtime: "mediapipe",
                                video: null,
                            });
                            render3D({
                                scaledMesh: keypoints.reduce((acc, pos) => {
                                    acc.push([pos.x, pos.y, pos.z]);
                                    return acc;
                                }, []),
                                Mesh3D: keypoints3D.reduce((acc, pos) => {
                                    acc.push([pos.x, pos.y, pos.z]);
                                    return acc;
                                }, []),
                                poseResult: poseResult
                            }, index);
                        } else {
                            render3D({ scaledMesh: [], Mesh3D: [] }, index);
                        }
                    }


                } catch (error) {
                    console.log('识别错误:', error);
                }
            };



            const startRecognition = async () => {
                await recognition();
                setInterval(recognition, 100);
            };

            const render3D = (data, index) => {
                renderer.render(scene, camera)


                updateGeometry(data, index);
            };

            const scene = new THREE.Scene()
            const geometrys = [];
            let camera = null;
            let renderer = null;


            const mappingTable = {
                rightArm: 'mixamorigRightForeArm',
                leftArm: 'mixamorigLeftForeArm',
                rightUpArm: 'mixamorigRightArm',
                leftUpArm: 'mixamorigLeftArm',
                rightUpLeg: 'mixamorigRightUpLeg',
                leftUpLeg: 'mixamorigLeftUpLeg',
                rightLeg: 'mixamorigRightLeg',
                leftLeg: 'mixamorigLeftLeg',
            }

            //const mappingTable = {
            //    rightArm: 'R_lowerarm',
            //    leftArm: 'L_lowerarm',
            //    rightUpArm: 'R_Upperarm',
            //    leftUpArm: 'L_Upperarm',
            //    rightUpLeg: 'R_thigh',
            //    leftUpLeg: 'L_thigh',
            //    rightLeg: 'R_calf',
            //    leftLeg: 'L_calf',
            //}
            const initThree = () => {
                const vw = video.value.videoWidth
                const vh = video.value.videoHeight


                const backgroundTexture = new THREE.VideoTexture(video.value)
                const background = new THREE.Mesh(
                    new THREE.PlaneGeometry(vw, vh),
                    new THREE.MeshBasicMaterial({
                        map: backgroundTexture
                    })
                )
                background.position.set(0, 0, -1000)
                scene.add(background)



                camera = new THREE.OrthographicCamera(
                    vw / -2, vw / 2, vh / 2, vh / -2, 0.1, 2000
                );

                camera.position.set(0, 0, 1000)
                camera.lookAt(scene.position)








                const hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.3)
                scene.add(hemiLight)
                const ambientLight = new THREE.AmbientLight(0xffffff, 0.7)
                scene.add(ambientLight)
                //const directionalLight = new THREE.DirectionalLight(0xffffff, 0.7)
                //directionalLight.position.set(0.5, 0, 0.866)
                //scene.add(directionalLight);
                // 添加光源
                const light = new THREE.DirectionalLight(0xffffff, 1);
                light.position.set(10, 10, 10);
                scene.add(light);








                renderer = markRaw(new THREE.WebGLRenderer({
                    canvas: document.getElementById("output"),
                    alpha: true,
                    antialias: true
                }))
                renderer.setPixelRatio(window.devicePixelRatio)
                let w = window.innerWidth - 16;
                let h = window.innerHeight - 130;
                const videoAspectRatio = vw / vh;
                if (w / h > videoAspectRatio) {
                    w = h * videoAspectRatio;
                } else {
                    h = w / videoAspectRatio;
                }
                renderer.setSize(w, h)


                const MODEL_PATH = 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/1376484/stacy_lightweight.glb';
                let stacy_txt = new THREE.TextureLoader().load('https://s3-us-west-2.amazonaws.com/s.cdpn.io/1376484/stacy.jpg');
                stacy_txt.flipY = false;

                const stacy_mtl = new THREE.MeshPhongMaterial({
                    map: stacy_txt,
                    color: 0xffffff,
                    skinning: true
                });
                //const MODEL_PATH = require("@/assets/VillagerA.glb").default;
                const loader = new GLTFLoader();
                loader.load(MODEL_PATH, function (gltf) {

                    const model = gltf.scene;
                    const bones = {};

                    model.traverse((o) => {
                        if (o.isMesh) {
                            o.castShadow = true;
                            o.receiveShadow = true;
                            o.material = stacy_mtl;
                        }

                        if (o.isBone) {
                            bones[o.name] = o;
                        }
                    });

                   
                    //bones['R_thigh'].rotation.z = THREE.MathUtils.degToRad(30);
                    //console.log(THREE.MathUtils.radToDeg(bones['R_calf'].rotation.x),

                    bones['mixamorigRightLeg'].rotation.z = THREE.MathUtils.degToRad(50);

                    //bones['R_calf'].rotation.x = THREE.MathUtils.degToRad(70);
                    //bones['R_calf'].rotation.z = THREE.MathUtils.degToRad(-0);
                    //bones['R_calf'].rotation.y = THREE.MathUtils.degToRad(30);
                 

                    const object = gltf.scene


                    const size = new THREE.Vector3();
                    const box = new THREE.Box3().setFromObject(object);
                    box.getSize(size);

                    object.scale.set(250, 250, 250);
                    //object.scale.set(30000, 30000, 30000);
                    //object.rotation.y = Math.PI ;
                    const skeletonHelper = new THREE.SkeletonHelper(object);
                    //dModel.add(object);
                 



                    geometrys.push({ size: size, model: object, skeletonHelper: skeletonHelper, bones: bones })
                    scene.add(object);

                    scene.add(skeletonHelper);

                    renderer.render(scene, camera)
                })

            };


            const getScale = (coords, id0 = 0, id1 = 1) => {
                const v1 = new THREE.Vector3(...coords[id0])
                const v2 = new THREE.Vector3(...coords[id1])
                return v1.distanceTo(v2)
            }


            const getRotation = (p0, p1, p2) => {
                const matrix = new THREE.Matrix4()
                const x = p1.clone().sub(p2).normalize()
                const y = p1.clone().add(p2).multiplyScalar(0.5).sub(p0).multiplyScalar(-1).normalize()
                const z = new THREE.Vector3().crossVectors(x, y).normalize()
                matrix.makeBasis(x, y, z)
                return matrix.invert()
            }

            const calculateMidpoint = (point1, point2) => {
                if (point1.length !== 3 || point2.length !== 3) {
                    throw new Error('Each point must have three coordinates (x, y, z).');
                }

                const midX = (point1[0] + point2[0]) / 2;
                const midY = (point1[1] + point2[1]) / 2;
                const midZ = (point1[2] + point2[2]) / 2;

                return [midX, midY, midZ];
            }




            function calculateRotationFromThreePoints(pointA, pointB, pointC) {
                // 計算從 A 到 B 和 A 到 C 的向量
                const vectorAB = new THREE.Vector3().subVectors(pointB, pointA).normalize();
                const vectorAC = new THREE.Vector3().subVectors(pointC, pointA).normalize();

                // 計算旋轉四元數
                const quat = new THREE.Quaternion().setFromUnitVectors(vectorAB, vectorAC);

                // 將四元數轉換為歐拉角
                const euler = new THREE.Euler().setFromQuaternion(quat);

                // 返回旋轉程度（以度為單位）
                return {
                    x: THREE.MathUtils.radToDeg(euler.x),
                    y: THREE.MathUtils.radToDeg(euler.y),
                    z: THREE.MathUtils.radToDeg(euler.z)
                };
            }



            function SetJointAnglesFromVects(
                joint,
                vec_parent_world,
                vec_child_world,
                lockAxis = null,
                axisMap = 'xyz',
                adjustmentAngles = { x: 0, y: 0, z: 0 }
            ) {
                const mapAxis = vector => new THREE.Vector3(vector[axisMap[0]], vector[axisMap[1]], vector[axisMap[2]]);
                const mapped_vec_parent_world = mapAxis(vec_parent_world);
                const mapped_vec_child_world = mapAxis(vec_child_world);

                // 獲取父物件的四元數並計算局部方向
                const parentQuat = new THREE.Quaternion();
                joint.parent.getWorldQuaternion(parentQuat);
                const vec_parent_local = mapped_vec_parent_world.clone().applyQuaternion(parentQuat.clone().invert()).normalize();
                const vec_child_local = mapped_vec_child_world.clone().applyQuaternion(parentQuat.clone().invert()).normalize();


                // 計算旋轉四元數
                const quat_pose_rot = new THREE.Quaternion().setFromUnitVectors(vec_parent_local, vec_child_local);
                const euler = new THREE.Euler().setFromQuaternion(quat_pose_rot);

                euler.x += adjustmentAngles.x;
                euler.y += adjustmentAngles.y;
                euler.z += adjustmentAngles.z;

                if (lockAxis) {
                    const originalEuler = new THREE.Euler().setFromQuaternion(joint.quaternion);
                    euler[lockAxis] = 0;// originalEuler[lockAxis];
                }

                quat_pose_rot.setFromEuler(euler);
                joint.quaternion.copy(quat_pose_rot);
                //joint.quaternion.rotateTowards(quat_pose_rot, 0.1);
            }




            const updateGeometry = (prediction, index) => {
                const vw = video.value.videoWidth
                const vh = video.value.videoHeight
                const faceMesh = resolveMesh(prediction.scaledMesh, vw, vh)
                const poseResult = prediction.poseResult
                const keypoints = prediction.Mesh3D;//faceMesh prediction.scaledMesh; // Mesh3D scaledMesh




                const bones = geometrys[index].bones;
                const object = geometrys[index].model;
                const size = geometrys[index].size;
                const position = calculateMidpoint(faceMesh[24], faceMesh[23]);
                const scaleX = getScale(prediction.scaledMesh, 11, 12)
                const scaleY = getScale(prediction.scaledMesh, 6, 30)
                const pose_left_shoulder = new THREE.Vector3(keypoints[11][0], -keypoints[11][1], -keypoints[11][2]);
                const pose_right_shoulder = new THREE.Vector3(keypoints[12][0], -keypoints[12][1], -keypoints[12][2]);
                const pose_left_elbow = new THREE.Vector3(keypoints[13][0], -keypoints[13][1], -keypoints[13][2]);
                const pose_right_elbow = new THREE.Vector3(keypoints[14][0], -keypoints[14][1], -keypoints[14][2]);
                const pose_left_hand = new THREE.Vector3(keypoints[15][0], -keypoints[15][1], -keypoints[15][2]);
                const pose_right_hand = new THREE.Vector3(keypoints[16][0], -keypoints[16][1], -keypoints[16][2]);
                const pose_left_hand_thumb_4 = new THREE.Vector3(keypoints[21][0], -keypoints[21][1], -keypoints[21][2]);
                const pose_right_hand_thumb_4 = new THREE.Vector3(keypoints[22][0], -keypoints[22][1], -keypoints[22][2]);
                const pose_left_hip = new THREE.Vector3(keypoints[23][0], -keypoints[23][1], -keypoints[23][2]);
                const pose_right_hip = new THREE.Vector3(keypoints[24][0], -keypoints[24][1], -keypoints[24][2]);
                const pose_hips = ((new THREE.Vector3).copy(pose_left_hip)).add(pose_right_hip).multiplyScalar(0.5);
                const pose_spine_2 = ((new THREE.Vector3).copy(pose_right_shoulder)).add(pose_left_shoulder).multiplyScalar(0.5); //.multiplyScalar(0.728);
                const pose_left_leg = new THREE.Vector3(keypoints[25][0], -keypoints[25][1], -keypoints[25][2]);
                const pose_left_foot = new THREE.Vector3(keypoints[27][0], -keypoints[27][1], -keypoints[27][2]);
                const pose_right_leg = new THREE.Vector3(keypoints[26][0], -keypoints[26][1], -keypoints[26][2]);
                const pose_right_foot = new THREE.Vector3(keypoints[28][0], -keypoints[28][1], -keypoints[28][2]);


                position[1] -= scaleY / 2;
                object.position.set(...position)
                ////object.scale.set(scaleX / size.x, scaleY / size.y, scaleY / 2 / size.x);
                ////object.scale.set(scaleX / size.x, scaleX / size.x, scaleX / size.x);

                var point_parent;
                var point_articulation;
                var point_child;
                var vec_parent;
                var vec_bone;

                //const rotation = getRotation(pose_spine_2, pose_left_hip, pose_right_hip)
                //object.rotation.setFromRotationMatrix(rotation)
                ////console.log('data', THREE.MathUtils.radToDeg(object.rotation.x), THREE.MathUtils.radToDeg(object.rotation.y), THREE.MathUtils.radToDeg(object.rotation.z))
                //object.rotation.y = -object.rotation.y;
                //object.rotation.x = -object.rotation.x;


                const topP = [(prediction.Mesh3D[11][0] + prediction.Mesh3D[12][0]) * 0.5,
                (prediction.Mesh3D[11][1] + prediction.Mesh3D[12][1]) * 0.5,
                (prediction.Mesh3D[11][2] + prediction.Mesh3D[12][2]) * 0.5]


                const rotation = getRotation(
                    new THREE.Vector3(...topP),
                    new THREE.Vector3(...prediction.Mesh3D[23]),
                    new THREE.Vector3(...prediction.Mesh3D[24])
                )

                object.rotation.setFromRotationMatrix(rotation)
                object.rotation.y = -object.rotation.y + Math.PI;
                object.rotateZ(-Math.PI)





                //point_parent = pose_hips;
                //point_articulation = pose_spine_2;
                //point_child = pose_right_elbow;

                //vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent).multiplyScalar(0.375);
                //vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                //SetJointAnglesFromVects(bones['mixamorigHead'], vec_bone, vec_parent);
       

                point_parent = pose_right_shoulder;
                point_articulation = pose_right_elbow;
                point_child = pose_right_hand;
                vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent);
                vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                SetJointAnglesFromVects(bones[mappingTable.rightArm], vec_parent, vec_bone, '');

                point_parent = pose_left_shoulder;
                point_articulation = pose_left_elbow;
                point_child = pose_left_hand;
                vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent);
                vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                SetJointAnglesFromVects(bones[mappingTable.leftArm], vec_parent, vec_bone, '');

 
         


                point_parent = pose_spine_2;
                point_articulation = pose_right_shoulder;
                point_child = pose_right_elbow;
                vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent);
                vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                SetJointAnglesFromVects(bones[mappingTable.rightUpArm], vec_parent, vec_bone, '');


                point_parent = pose_spine_2;
                point_articulation = pose_left_shoulder;
                point_child = pose_left_elbow;
                vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent);
                vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                SetJointAnglesFromVects(bones[mappingTable.leftUpArm], vec_parent, vec_bone, '');




                point_parent = pose_hips;
                point_articulation = pose_right_hip;
                point_child = pose_right_leg;
                vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent);
                vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                SetJointAnglesFromVects(bones[mappingTable.rightUpLeg], vec_parent, vec_bone, '', 'xyz', { x: 0, y: 0, z: Math.PI / 2 });


                point_parent = pose_right_hip;
                point_articulation = pose_right_leg;
                point_child = pose_right_foot;
                vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent);
                vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                SetJointAnglesFromVects(bones[mappingTable.rightLeg], vec_parent, vec_bone, '');


                point_parent = pose_hips;
                point_articulation = pose_left_hip;
                point_child = pose_left_leg;
                vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent);
                vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                SetJointAnglesFromVects(bones[mappingTable.leftUpLeg], vec_parent, vec_bone, '', 'xyz', { x: 0, y: 0, z: -Math.PI / 2 });


                point_parent = pose_left_hip;
                point_articulation = pose_left_leg;
                point_child = pose_left_foot;
                vec_parent = (new THREE.Vector3).subVectors(point_articulation, point_parent);
                vec_bone = (new THREE.Vector3).subVectors(point_child, point_articulation);
                SetJointAnglesFromVects(bones[mappingTable.leftLeg], vec_parent, vec_bone, '');
            }


            const resolveMesh = (faceMesh, vw, vh) => {
                return faceMesh.map(p => [p[0] - vw / 2, vh / 2 - p[1], -p[2]])
            }

            onMounted(async () => {
                await setupCamera();
                await createModel();
                initThree();
            });


            return {
                video,
                startRecognition,
            };
        },
    };
</script>

<style scoped>
    video {
        width: 100%;
        height: auto;
    }
</style>
